<template>
      <div class="m_home">
         <van-tabs v-model="active"   ref="vanlist" :sticky="true" color="#4bcf96" class="list_inner">
          <van-tab title="会议室列表" >
            <van-list
                v-model="loading"
                :finished="finished" 
                @load="onLoad"
            >

            <div v-for="(i,k) in tableData" class="my_reservation"  :key=k  @click="goDetail(i.id, 'detail')"> 
                <div class="row_box row_box1" >
                    <div class="flex_left">
                        <img  v-lazy="i.cover_photoUrl.op_opic" class="list_img" />
                    </div>
                    <div class="flex_right">
                        <div class="m_title">
                            {{i.meetingroom_name}}
                        </div>
                        <div class="m_content">
                            可容纳人数：{{i.meetingroom_size}}人 <br>
                            面积：{{i.meetingroom_info}}平方米 <br>
                            价格：{{i.price}}元/每小时
                        </div>
                    </div>
                </div>  
            </div>  
            <no-data :open="finished"></no-data>
            </van-list>
             </van-tab>
          <van-tab title="我的预约">
            <van-list
                v-model="loading1"
                :finished="finished1" 
                @load="onLoad1"
            >

            <div v-for="(i,k) in tableData1" class="my_reservation"  :key=k  @click="goDetail(i.reservation_id,'order')"> 
                <div class="row_box row_box1" > 
                    <div class="flex_left">
                        <img  v-lazy="i.cover_photo.op_opic"  class="list_img"  />
                    </div>
                    <div class="flex_right">
                        <div class="m_title">
                            {{i.meetingroom_name}}
                        </div>
                        <div class="m_content">
                            可容纳人数：{{i.meetingroom_size}}人 <br>
                            面积：{{i.meetingroom_info}}平方米 <br>
                            价格：{{i.price}}元/每小时
                        </div>
                    </div>
                </div>  
                <div class="res_times">预订时间：{{i.time}} <span :class="{active: i.state == 3}" v-html="i.state == 0 ? '待审核' : i.state == 1? '通过' : i.state == 2?'已拒绝': '已取消'"></span></div>
            </div>  
             <no-data :open="finished1"></no-data>
            </van-list>
          </van-tab>
           </van-tabs>
        </div>
</template>
<script>
  import * as utils from '@/common/utils.js'
   import NoData from "@/components/NoData.vue";
  export default {
    name: 'm_home',
    data() {
      return {
          active: 0,
          tableData: [],
          tableData1: [],
          pageSize: 10,
          page: 1, 
          loading: false,
          finished: false,
          loading1: false,
          finished1: false,
          page1: 1
      }
    },
    created() {
        this.zone_id = utils.getQueryString(window.location.href, 'zone_id') || 1; 
        this.getUser();
    },
    components: {NoData},
    watch: {
        'active': function() { 
            if(this.active == 0) {
                if (process.env.NODE_ENV === 'development') { 
                    location.assign('/meeting/index.html/#/')
                } else { 
                    location.assign('/user/meeting/index.html#/')
                }
            }
             document.documentElement.scrollTop = document.body.scrollTop = 0;
        }
    },
    methods: {
        getUser() {
          this.axios.post('/app/appuser/getuserinfo',{}).then(res => {
                if (res.s == 1) {  
                    sessionStorage.users= JSON.stringify({
                      name: res.d.username,
                      phone: res.d.phone,
                      company: res.d.enterprise_name,
                    })
                } 
            }) 
        },
        onLoad() { 
            this.axios.get('/app/meeting/lists?zone_id='+this.zone_id+'&page='+this.page+'&pageSize='+this.pageSize).then(res => {
            this.loading = false;
            if (res.s == 1) { 
              this.tableData = this.tableData.concat(res.d.data);
              if(res.d.last_page <= this.page) {
                  this.finished = true;
              }else {
                this.page++;
              }
            }else {
              this.finished = true;
            }
          })  
        },
        onLoad1() { 
            this.axios.get('/app/meeting/myReservation?zone_id='+this.zone_id+'&page='+this.page1+'&pageSize='+this.pageSize).then(res => {
            this.loading1 = false;
            if (res.s == 1) { 
              this.tableData1 = this.tableData1.concat(res.d.data);
              if(res.d.last_page <= this.page1) {
                  this.finished1 = true;
              }else {
                this.page1++;
              }
            }else {
              this.finished1 = true;
            }
          })  
        },
        goDetail(id,type) {
              this.$router.push({
                path: '/detail/'+id+'/type/'+type,
              })
        }
        
    },
    beforeRouteEnter (to, from, next) {
        if((from.name == 'mDetail' && from.params.mtype == 'order') || from.name == 'mReservation') {
            next(vm=>{
                vm.active = 1;
            })
            return;
        }
        next()


    }
  }
</script>
<style lang="scss">
@function rem($px) {
    @return $px / 50px * 1rem;
}
  .m_home { 
    .row_box {
        display: flex;
        .flex_left {
            width: rem(142px);
            img {
                border-radius: 6px;

            }
        }
        .flex_right {
            flex: 1;
            padding-left: rem(15px);
            .m_title {
                -webkit-line-clamp: 2;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-box-orient: vertical;

            }
            .m_content {
                margin-top: rem(12px);
                font-size: rem(14px);
                color: #8a8c89;
            }
        }
    }
    .row_box1 {
          padding: rem(15px) rem(15px); 
    }
    .my_reservation {
        margin-bottom: rem(8px);
        background: #fff; 
    }
    .res_times {
          padding: rem(15px) rem(15px); 
          font-size: rem(14px);
          border-top: rem(1px) solid #eee;
          padding-top: rem(15px);
          span {
              float: right;
              color: #4bcf96;
              &.active{
                color: #d0d2cf;

              }
          }

      }
    .van-tabs__wrap {
        height: rem(55px);
        line-height: rem(55px); 
        background: #fff;
      }
     .van-tab {
        font-size: rem(15px);
        height: rem(55px);
        line-height: rem(55px);
        border-right: 1px solid #eee;
        color: #d0d2cf;
        font-size: rem(16px); 
        border-bottom: rem(1px) solid #eee;
      }
      .van-tabs--line {
          padding-top: rem(55px);
           

      } 
      .van-tab--active {
           border-bottom: 2px solid #4bcf96;
      }
      .van-tabs__line {
          z-index: 0;
      }
      .list_img {
          width: rem(140px);
          height: rem(100px);

      }

}
  
</style>

